@import "../common/styles/saas/variables";
@import "../common/styles/saas/mixins";

.projectsBlock {
  min-height: 100vh;
  padding: 150px 0;

  .projectsContainer {
    @include container();
    @include flex(column,center,center,nowrap);

    .projects {
      @include flex(row,space-evenly,stretch,wrap);
      width: 100%;
      margin-top: 30px;

      @media screen and (max-width: 1003px) {
        justify-content: center;
      }
    }
  }
}




